<template>
	<div id="single-blog"  class="bg-ligrey">
		
		<div v-if="msgState" v-theme:column="'middle'" class="bg-white mb-5">
	      <div class="pd-10 fs12 c-red el-icon-warning"><span class="pl-10 c-red">{{stateMessage}}</span></div>
	    </div>
		<div class="rel bg-white">
			<div class="edit" v-if="editshow">
				<div class="iconfont icon-compile-alt" @click="edit"><span class="fs12 c9 pl-10 fw-b">编辑</span></div>
			</div>
			<div class="fs20 fw-b ta-c pd-20">{{blog.title}}</div>
			<textarea class="ti-20 fs14 mb-10" style="height: 28.333333rem; border:none;">{{blog.content}}</textarea>
			<div class="pd-20">
				<div class="fs12  fs-inta c9 pb-5"><span class="pr-10 c9">Author:</span>{{blog.author}}</div>
				<div>
				<div class="pr-10 c9 fs12 fs-inta pb-5">Category:<span class="fs12 fs-inta c9 pl-10">{{blog.category}}</span></div>
				</div>	
				<div class="fs12 fs-inta c9">Published time:<span class="fs12 fs-inta c9 pl-10">{{blog.blogdate}} {{blog.blogtime}}</span></div>	
				<div class="dis-box justify-content-between ">
					<div class="fs12  fs-inta c9">Updated time:<span class="fs12 fs-inta c9 pl-10">{{blog.updateDate}} {{blog.updateTime}}</span> </div>	
					<button @click="deleteBlog" class="btn-block mr-10 fs12" v-if="editshow">删除</button>	
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	export default {
		name:'single-blog',
		data(){
			return{
				blogid:this.$route.params.blogid,
				blog:{},
				blogaccount:'',
				stateMessage:'',
				msgState: false,
				

			}
		},
		computed:{
			loginstatus(){
				return this.$store.state.loginstatus
			},
			phone(){
				return this.$store.state.phone

			},
			ctype(){
				return this.$store.state.ctype
			},
			editshow(){
				if(this.ctype==1 || this.ctype==0&&this.blogaccount ==this.phone){
					return true
				}else{
					return false
				}
			},
			
			

		},
		created(){
			console.log(this.$route.params.blogid)
			this.$http.get('/Aposts/'+this.blogid+'.json').then((res)=> {
				console.log(888,res.data)
				this.blog = res.data
				this.blogaccount = res.data.phone
			})


			
		},
		mounted(){
			// this.$http.get('/BlogUsers.json').then(res=>{
			// 	let users = []
			// 	for(let key in res.data){
			// 		users.push(res.data[key])
			// 	}
			// 	// console.log(767,users)
			// 	let result = users.filter((user)=>{
			// 		return user.phone == this.phone
			// 	})
			// 	// console.log(666,result)
			// 	if(result.length>0){
			// 		this.ctype = result[0].ctype
			// 	}return
				

			// })

		},
		methods:{
			deleteBlog(){
				if(this.loginstatus == null){
					//未登录让其登录
					this.$store.commit("setPopLog",{LogisShow:true,nav:1})
				}else{

					if(this.ctype==0 && this.blogaccount !== this.phone){

						// console.log('他人文章无权限删除')
						this.msgState = true
						this.stateMessage ="他人文章无权删除!"

					}else{
						this.$http.delete('/Aposts/'+this.blogid+'.json')
						.then(res=>{
							this.$store.commit('popMessage',{msg:"删除成功",success:true})
							setTimeout(()=>{
								this.$store.commit('popMessage',{msg:"",success:false})
								this.$router.push({path:'/'}) //删除成功跳转到主页
							},1000)
						})
					}
				}
				// if(this.loginstatus == true && this.blogaccount == this.phone){
				// 	this.$http.delete('https://wd2206394391jwoklu.wilddogio.com/Aposts/'+this.id+'.json').then(res=>{
				// 	this.$router.push({path:'/'}) //删除成功跳转到主页
				// 	})
				// }else{
				// 	// console.log('未登录')
				// 	this.$store.commit("setPopLog",{LogisShow:true,nav:1})
				// }				
			},
			edit(){
				// console.log(66,this.phone)
				// console.log(67,this.loginstatus)
				// console.log(77,this.id)
				if(this.loginstatus == null || this.loginstatus == false){
					//未登录让其登录
					this.$store.commit("setPopLog",{LogisShow:true,nav:1})
				}else{

					if( this.ctype==0 && this.blogaccount !== this.phone){


						this.msgState = true
						this.stateMessage ="他人文章无权编辑!"

					}else{
						console.log(this.blogid)
						this.$router.push({path:'/edit',query:{blogid:this.blogid}})
					}
				}
			},
			
		}
	}
</script>

<style scoped>
	#single-blog{
		/* position: relative; */
		box-sizing: border-box;
		margin: 0 auto;
		padding: 10px 10px 20px;
		max-width: 960px;
	    background: #f6f6f6;
	}
	.edit{
		position: absolute;
		top: 10px;
		left: 10px;
	}
	article{
		text-indent: 20px;
		padding: 10px;
	}
	.tit{
		font-size: 24px;
		padding:10px 0 20px;
		/* font-weight: bold; */
	}
	.bg-ligrey{
		background: #f6f6f6;
	}
	.heart{
		position: absolute;
		top: 1.111111rem;
		right: 2.5rem;
	}
	.textarea{
		height: 18.3333rem;
	    border: none;
	    padding: 0 20px;
	}
</style>


