<template>
  <div  v-theme:column="'middle'" id="show-blogs" class="mt-10 blogs ">
    <div class="dis-box justify-content-between bb pd-5 bg-white">
      <div class="pl-20 fw-b fs18 ">ARTICLES</div>
      <div class="seainpu">
          <input type="text" placeholder="搜索" v-model="search">
          <i class="iconfont icon-sousuo1 c9" @click="gosearch"></i>
      </div>
    </div> 
    <!-- 博客列表 -->
    <div class="allblogs bg-white" >
      <div 
        v-infinite-scroll="loadMore"
        infinite-scroll-disabled="loading" 
        infinite-scroll-distance="30" 
        infinite-scroll-immediate-check = false
        class="plr-20">
        <div class="single-blog"  v-for="blog in showBlogs">
          <router-link :to="'/blog/'+blog.blogid">
            <h4 class="iconfont icon-wenxue pr-10" >&nbsp{{blog.title}}</h4> 
            <article class="c9 ">
              {{blog.content | snippet }}
            </article>
            <div class="dis-box pt-10" style="height: 1.7rem;">
              
              <div class="cate fs12 pr-10" v-if="blog.updateDate">Updated:<span class="cate fs12 pl-5">{{blog.updateDate}} {{blog.updateTime}}</span></div>
              <div class="cate fs12">{{blog.category}}</div>
            </div>
          </router-link>          
        </div>
        <mt-spinner type="triple-bounce" v-show="loadingShow" color="#999" class="ta-c pd-10"></mt-spinner>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: 'show-blogs',
  data(){
    return{
      blogs:[],
      search:'',
      totalpNum:'',//页码总数
      showBlogs:[],
      filteredBlogs:[],
      page:1,//当前页码
      loadingShow:null,
    }
  }, 
  created(){ 
    this.$http.get('/Aposts.json').then(res=>{
        // console.log(777,res.data)
      let blogsArray = [];
      for (let key in res.data){
        res.data[key].blogid = key
        blogsArray.push(res.data[key])
      }
      this.blogs = blogsArray 
      this.totalpNum = blogsArray.length / 5 //每页展示5项
      this.showBlogs = blogsArray.slice(0,5)
      // console.log(323,this.blogs)
      console.log(323,blogsArray.length)
      //取数组中的前10个对象，起始index是0，终止Index是10，
      //包含index为0的对象，不包含index为10的对象
    })
  },
  watch:{

  },
  methods:{
      loadMore(){
        this.page+=1
        if(this.page >= this.totalpNum){
          this.loading = true 
          console.log('已到底部，无更多数据')
        }else{
          this.loading = true
          this.loadingShow =true
          setTimeout(() => {
            console.log('page',this.page)
            this.showBlogs = this.blogs.slice(0,this.page*5)
            // console.log(3232,this.showBlogs)
            this.loadingShow =false
            this.loading = false;
          }, 1000);
        }
        
      },
      gosearch(){
        this.filteredBlogs = this.blogs.filter((blog)=>{
          return blog.title.match(this.search)
        })
        this.$store.commit('getSearchBlogs',this.filteredBlogs)
        this.$router.push("/search")
        //可以通过路由传值，但数据是一个对象，浏览器地址会跟上很长一串字符串，故改为vuex传值。
        // this.$router.push({path:'/search',query:{filteredBlogs:this.filteredBlogs}})
      }   
  },
  filters: {
      snippet: function (value) {
       return value.slice(0,140)+"..."
      }
    }
  }
</script>

<style >

#show-blogs{  
  box-shadow: 1px 1px 2px #eee;
}
.single-blog{
  padding: 20px 0;
  box-sizing: border-box;
  border-bottom: solid 1px #eee;
  height: 9.2rem;
  overflow: hidden;
}
a{
  text-decoration: none;
}
.seainpu{
    text-align: right;
}
.seainpu input{
    border: none;
    padding: 4px 12px;
    background: #eee;
    border-radius: 15px;
}
.blogs article{
    padding-top: 10px;
    color: #999;
    font-size: 14px;
}
.cate{
    font-size: 14px;
    color: #999;
    padding-right: 10px;
    font-style: italic;
}
article{
  height: 4rem;
  overflow: hidden;
}
</style>



