<template>
	<div v-theme:column="'middle'">
		<div class="ta-c pd-10 c6 fw-b">头像上传</div>
		<div class="rel">
			<div class="upload_add dis-box justify-content-center flex-direction-column b" @click="fileClick "  v-if="addShow">
				<div class="iconfont icon-shangchuan1 c9" style="font-size: 300%"></div>
			</div>
			<input type="file" @change="fileChange($event)" id="upload_file" multiple style="display: none;">
			<i class="delimg el-icon-circle-close fs20" @click="fileDel" v-if="!addShow"></i>
			<div id="result" class="upload_preview " v-if="!addShow">
				<div class="img_wrap">
					<img :src="showimg_src" alt="" >
				</div>
			</div>	
		</div>
		<button @click="submitphoto" class="btn-block fs12 mb-30">确认上传</button>
		<div class="pb-20"></div>
	</div>
	
</template>
<script>
	export default{
		data(){
			return{
				showimg_src:null,
				showimg_name:'',
				addShow: true,
				list:[],
				rs_User:{},
				userid:''
			}
		},
		methods:{
			fileClick() {
		        document.getElementById('upload_file').click()
		    },
		    fileChange(el) {
		        if (!el.target.files[0].size) return;
		        this.addFile(el.target)
		        el.target.value=""
		    },
		    addFile(element){
		    	this.addShow = false
		    	let file = element.files[0]
	     		this.showimg_name = file.name
	     		let reader = new FileReader()
	     		reader.readAsDataURL(file)
	     		let _this = this
	    		reader.onload = function(event){
	    			// console.log(this.result)
	    			_this.showimg_src = this.result
	    		}
		    },
			dragenter(el) {
				el.stopPropagation();
				el.preventDefault();
			},
			dragover(el) {
				el.stopPropagation();
				el.preventDefault();
			},
			drop(el) {
				el.stopPropagation();
				el.preventDefault();
				this.fileList(el.dataTransfer);
			},
		    fileDel(){
		    	this.showimg_src =""
		    	this.addShow = true
		    },
		    fileAdd(file){
		    	if(file.type.indexOf('image')== -1){
		    		file.src ='wenjian.png'

		    	}else{
		    		let reader = new FileReader()
		    		let image = new Image()
		    		let _this= this
		    		let result =reader.readAsDataURL(file)
		    		console.log(result)
		    		console.log(result)
		    		reader.onload = function(){
		    			file.src = this. result
		    		}
		    	}
		    },
		    submitphoto(){
		    	this.$http.get('/BlogUsers.json').then(res=>{
		    		let resArr = []
		    		let phone = this.$store.state.phone
		    		for(let key in res.data){
		    			resArr.push(res.data[key])
		    		}
		    		let cUserArr = resArr.filter((item)=>{
		    			return item.phone === phone
		    		})
		    		let cUser =cUserArr[0]
		    		
		    		console.log(55,resArr)
		    		console.log(66,cUser.headshot)
		    		for(let prop in cUser){
		    			if(prop == 'headshot'){
		    				cUser.headshot = this.showimg_src
		    			}
		    		}
		    		console.log(77,cUser)
		    		this.rs_User =cUser
		    		console.log(77,this.rs_User)
		    		this.userid = cUser.cid
		    		console.log(this.userid)
			    	this.$http.put('/BlogUsers/'+this.userid+'.json',this.rs_User).then(res=>{
			    		console.log(666,res.data)
			    		this.$store.commit('setHeadShot',this.showimg_src)
			    		this.$router.push('/user')
			    	})
		    	})
		    },
		    post(){
		    	alert(1)
		    	// userid = this.$store.state.cid
		    	// this.$http.put('/BlogUsers'+userid+'.json',data).then(res=>{
		    	// 	console.log(res.data)
		    	// })
		    }

		}
	}
</script>
<style scoped>
.upload_add{
	position: absolute;
	z-index: 5;
	width: 11.111111rem;
	height: 11.111111rem;
}
.img_wrap{
	width: 11.111111rem;
	height: 11.111111rem;
}
.img_wrap img{
	width: 100%;
	height: 100%;
}
.upload_preview{
	position: absolute;
	z-index: 2;
	width: 11.111111rem;
	height: 11.111111rem;
}
.delimg{
	position: absolute;
	top: 2px;
	right: 2px;
	z-index: 6;
}
.rel{
	width: 11.111111rem;
	height: 11.111111rem;
	margin: 0 auto;
	padding-bottom: 1.111111rem;
}
.btn-block{
	width: 78px;
	margin: 0 auto;
}
</style>


